"use client";

import { useState, useEffect } from "react";
import {
  Box,
  CircularProgress,
  Container,
  Grid,
  Typography,
  useTheme,
} from "@mui/material";
import ChapterList from "@/components/DSA/ChapterList";
import ChapterDetail from "@/components/DSA/ChapterDetail";
import ProgressTracker from "@/components/DSA/ProgressTracker";
import api from "@/apiHandler/page";

export default function DSAPage() {
  const theme = useTheme();
  const [topics, setTopics] = useState([]);
  const [selectedTopic, setSelectedTopic] = useState(null);
  const [completedSubtopicIds, setCompletedSubtopicIds] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  const fetchData = async () => {
  try {
    setLoading(true);
    console.log("Fetching topics and completed subtopics...");

    const [topicsRes, completedRes] = await Promise.all([
      api.get("/topics/all"),
      api.get("/subtopics/completed"),
    ]);

    console.log("Topics response:", topicsRes);
    console.log("Completed subtopics response:", completedRes);

    setTopics(topicsRes.data || []);

    // Handle both array and object responses
    const completedData = Array.isArray(completedRes.data) 
      ? completedRes.data 
      : completedRes.data?.completed || completedRes.data?.items || [];
    
    const completedIds = completedData.map(item => 
      typeof item === 'string' ? item : item._id || item.id
    ).filter(Boolean);

    console.log("Completed subtopic IDs:", completedIds);
    setCompletedSubtopicIds(completedIds);

    // Select first topic by default if none selected
    if (topicsRes.data?.length > 0 && !selectedTopic) {
      setSelectedTopic(topicsRes.data[0]);
    }
  } catch (err) {
    console.error("Error details:", {
      message: err.message,
      response: err.response?.data,
      status: err.response?.status,
    });
    setError("Failed to load data. Please try again later.");
  } finally {
    setLoading(false);
  }
};

  useEffect(() => {
    fetchData();
  }, []);

  const handleTopicSelect = (topic) => {
    setSelectedTopic(topic);
  };

  const handleUpdate = async () => {
    try {
      const response = await api.get("/subtopics/completed");
      setCompletedSubtopicIds(response.data?.map((item) => item._id) || []);
    } catch (err) {
      console.error("Error refreshing completed subtopics:", err);
    }
  };

  if (loading && topics.length === 0) {
    return (
      <Container maxWidth="xl" sx={{ py: 4 }}>
        <Box display="flex" justifyContent="center" p={4}>
          <CircularProgress />
        </Box>
      </Container>
    );
  }

  if (error) {
    return (
      <Container maxWidth="xl" sx={{ py: 4 }}>
        <Box p={4} textAlign="center">
          <Typography color="error">{error}</Typography>
        </Box>
      </Container>
    );
  }

  return (
    <Container maxWidth="xl" sx={{ py: 4 }}>
      <Typography variant="h4" component="h1" gutterBottom>
        DSA Learning Path
      </Typography>
      <Typography variant="subtitle1" color="text.secondary" paragraph>
        Master Data Structures and Algorithms with our comprehensive guide
      </Typography>

      <Grid container spacing={3} sx={{ mt: 2 }}>
        <Grid item xs={12} md={4} lg={3}>
          <Box
            sx={{
              position: "sticky",
              top: 16,
              maxHeight: "calc(100vh - 120px)",
              overflowY: "auto",
              "&::-webkit-scrollbar": {
                width: "6px",
              },
              "&::-webkit-scrollbar-track": {
                background: theme.palette.grey[100],
                borderRadius: "10px",
              },
              "&::-webkit-scrollbar-thumb": {
                background: theme.palette.grey[400],
                borderRadius: "10px",
                "&:hover": {
                  background: theme.palette.grey[500],
                },
              },
            }}
          >
            <ChapterList
              topics={topics}
              selectedTopic={selectedTopic}
              onSelectTopic={handleTopicSelect}
              completedSubtopicIds={completedSubtopicIds}
            />
          </Box>
        </Grid>

        <Grid item xs={12} md={8} lg={6}>
          {selectedTopic ? (
            <ChapterDetail
              chapter={selectedTopic}
              subtopics={selectedTopic.subtopics || []}
              onUpdate={handleUpdate}
            />
          ) : (
            <Box
              sx={{
                display: "flex",
                alignItems: "center",
                justifyContent: "center",
                minHeight: 400,
                bgcolor: "background.paper",
                borderRadius: 2,
                p: 4,
                border: `1px solid ${theme.palette.divider}`,
              }}
            >
              <Typography color="text.secondary">
                Select a chapter to view details
              </Typography>
            </Box>
          )}
        </Grid>

        <Grid item xs={12} lg={3}>
          <Box
            sx={{
              position: "sticky",
              top: 16,
            }}
          >
            <ProgressTracker
              topics={topics}
              completedSubtopicIds={completedSubtopicIds}
            />
          </Box>
        </Grid>
      </Grid>
    </Container>
  );
}
